<template>
    <div>
        <top-nav title="积分订单发货"></top-nav>
        <div class="wyu-main" v-loading="loading">
            <div style="min-width: 1600px">
                <div class="wyu-block">
                    <vue-easy-print tableShow ref="easyPrint">
                        <template>
                            <el-row :gutter="10" style="padding: 10px;width: 90%;margin: 0 auto;border: 1px solid #f2f2f2">
                                <el-col :span="24">
                                    <div class="wyu-title" style="position: revert">
                                        <h2>基本信息</h2>
                                        <div style="position: absolute;right: 10px;top: 16px">
                                            <el-button size="mini" icon="el-icon-printer" @click="printDemo">打印订单
                                            </el-button>
                                        </div>
                                    </div>
                                    <el-form label-position="left" inline style="padding: 0" class="demo-table-expand">
                                        <el-form-item label="订单 ID：">
                                            <span>{{detail.order_id}}</span>
                                        </el-form-item>
                                        <el-form-item label="订单号：">
                                            <span>{{detail.order_sn}}</span>
                                        </el-form-item>
                                        <el-form-item label="会员：">
                                            <span>{{detail.user.nickname}}(ID:{{detail.user.user_id}})</span>
                                        </el-form-item>
                                        <el-form-item label="电话：">
                                            <span>{{detail.user.mobile}}</span>
                                        </el-form-item>

                                        <el-form-item label="下单时间：">
                                            <span>{{detail.add_time}}</span>
                                        </el-form-item>
                                        <el-form-item label="支付时间：">
                                            <span>{{detail.pay_time}}</span>
                                        </el-form-item>
                                        <el-form-item label="物流公司：">
                                            <el-select v-model="form.shipping_id" size="mini" placeholder="请选择">
                                                <el-option :key="0" label="请选择" :value="0">
                                                </el-option>
                                                <el-option
                                                        v-for="item in shipping"
                                                        :key="item.shipping_id"
                                                        :label="item.shipping_name"
                                                        :value="item.shipping_id">
                                                </el-option>
                                            </el-select>
                                        </el-form-item>
                                        <el-form-item label="配送单号：">
                                            <span><el-input v-model="form.shipping_num" size="mini"></el-input></span>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                                <el-col :span="24">
                                    <div class="wyu-title">
                                        <h2>收货信息</h2>
                                    </div>
                                    <el-form label-position="left" inline style="padding: 0" class="demo-table-expand">
                                        <el-form-item label="收货人：" style="width: 10%">
                                            <span>{{detail.name}}</span>
                                        </el-form-item>
                                        <el-form-item label="联系方式：" style="width: 15%">
                                            <span>{{detail.mobile}}</span>
                                        </el-form-item>
                                        <el-form-item label="收货地址：" style="width: 50%">
                                            <span>{{detail.address}}</span>
                                        </el-form-item>
                                        <el-form-item label="留言：" style="width: 100%">
                                            <span style="font-size: 12px;">
                                                {{detail.user_note == '' ? '无' : detail.user_note}}
                                            </span>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                                <el-col :span="24">
                                    <div class="wyu-title">
                                        <h2>商品信息</h2>
                                    </div>
                                    <el-table size="mini" :data="goods" border style="width: 100%">
                                        <el-table-column prop="goods_sn" label="商品编号" width="120"></el-table-column>

                                        <el-table-column label="缩略图" width="70">
                                            <template slot-scope="scope">
                                                <img width="50" height="50" :src="scope.row.goods_photo">
                                            </template>
                                        </el-table-column>
                                        <el-table-column label="商品">
                                            <template slot-scope="scope">
                                                <div class="ellipsis-02">
                                                    {{scope.row.goods_name}}
                                                </div>
                                            </template>
                                        </el-table-column>

                                        <el-table-column label="数量" width="50">
                                            <template slot-scope="scope">
                                                X 1
                                            </template>
                                        </el-table-column>
                                    </el-table>
                                </el-col>
                                <el-col :span="24">
                                    <div class="wyu-title">
                                        <h2>操作信息</h2>
                                    </div>
                                    <el-form label-position="left" inline style="padding: 0" class="demo-table-expand">
                                        <el-form-item style="width: 100%" label="管理员备注">
                                            <span>{{detail.admin_note}}</span>
                                            <el-button @click="noteDialog = true" type="text" style="margin-left: 10px">
                                                修改
                                            </el-button>
                                        </el-form-item>
                                        <el-form-item style="width: 100%" label="操作">
                                            <el-button  @click="delivery" size="mini" style="margin-left: 10px">
                                                确认发货
                                            </el-button>
                                        </el-form-item>
                                    </el-form>
                                </el-col>
                            </el-row>
                        </template>
                    </vue-easy-print>
                </div>
            </div>
        </div>

        <!--备注操作弹窗-->
        <el-dialog title="管理员备注" :visible.sync="noteDialog" width="500px" center
                   :modal-append-to-body="false">
            <el-form size="mini" style="padding: 0" v-loading="noteLoading">
                <el-form-item>
                    <el-input type="textarea"
                              :rows="5" v-model="detail.admin_note"></el-input>
                </el-form-item>
                <el-form-item style="text-align: right">
                    <el-button size="mini" @click="noteDialog = false">取 消</el-button>
                    <el-button size="mini" type="primary" @click="setNote">确 定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

    </div>
</template>

<script>
    import * as api from '@/api.js';
    import vueEasyPrint from "vue-easy-print";
    import TopNav from "@/components/TopNav";

    export default {
        name: "FahuoDan",
        components: {TopNav, vueEasyPrint},
        data: function () {
            return {
                loading: false,
                noteLoading: false,
                noteDialog: false,
                detail: {
                    user: {},
                },
                shipping:[],
                goods: [],
                form:{
                    shipping_id:0,
                    shipping_num:'',
                },
            };
        },
        created() {
            this.getOrderDetail();
        },
        methods: {
            setNote() {
                this.noteLoading = true;
                api.post("/shop/admin.order.integralorder/adminNote", {
                    appToken: this.$store.getters.appToken,
                    note: this.detail.admin_note,
                    order_id: this.$route.params.id,
                }).then(() => {
                    this.noteLoading = false;
                    this.noteDialog = false;
                }).catch(() => {
                    this.noteLoading = false;
                });
            },
            //确认订单
            delivery() {
                this.$confirm('请仔细检查发货信息', '温馨提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    api.post("/shop/admin.order.integraldelivery/delivery", {
                        appToken: this.$store.getters.appToken,
                        order_id: this.$route.params.id,
                        shipping_id:this.form.shipping_id,
                        shipping_num:this.form.shipping_num,
                    }).then(() => {
                        this.$router.push("/delivery/integralorder/index");
                    }).catch(() => {

                    });
                }).catch(() => {
                });
            },
            printDemo() {
                this.$refs.easyPrint.print()
            },
            getOrderDetail() {
                this.loading = true;
                api.post("/shop/admin.order.integraldelivery/detail", {
                    appToken: this.$store.getters.appToken,
                    order_id: this.$route.params.id,
                }).then(res => {
                    this.detail = res.detail;
                    this.goods = res.goods;
                    this.shipping = res.shipping;
                    this.form.shipping_id = res.shipping_id;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            }
        }
    };
</script>
<style scoped>

    .demo-table-expand {
        font-size: 0;
    }

    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 25%;
    }
</style>
